class OneOutOfManyStatement {
  constructor() {
    this.commitments = [];
  }

  set(commitments) {
    this.commitments = commitments
  }
}


class OneOutOfManyWitness {
  constructor() {
    this.rand = new bn(0);
    this.indexIsZero = 0;
    this.stmt = new OneOutOfManyStatement();
  }

  set(commitments, rand, indexIsZero) {
    this.rand = rand;
    this.indexIsZero = indexIsZero;
    this.stmt.set(commitments);
  }

  prove() {
    // Check the number of Commitment list's elements
    let N = this.stmt.commitments.length;
    if (N !== constants.CM_RING_SIZE) {
      return {
        proof: new OneOutOfManyProof(),
        err: new Error("the number of Commitment list's elements must be equal to CM_RING_SIZE")
      }
    }

    let n = constants.CM_RING_SIZE_EXP;

    // Check indexIsZero
    if (this.indexIsZero > N) {
      return {
        proof: new OneOutOfManyProof(),
        err: new Error("Index is zero must be Index in list of commitments")
      }
    }

    // represent indexIsZero in binary
    let indexIsZeroBinary = utils.convertIntToBinary(this.indexIsZero, n);

    // randomness array
    let r = new Array(n); // big int array
    let a = new Array(n); // big int array
    let s = new Array(n); // big int array
    let t = new Array(n); // big int array
    let u = new Array(n); // big int array

    let cl = new Array(n); // elliptic point array
    let ca = new Array(n); // elliptic point array
    let cb = new Array(n); // elliptic point array
    let cd = new Array(n); // elliptic point array

    for (let j = 0; j < n; j++) {
      // Generate random numbers
      r[j] = utils.randScalar();
      a[j] = utils.randScalar();
      s[j] = utils.randScalar();
      t[j] = utils.randScalar();
      u[j] = utils.randScalar();

      // convert indexIsZeroBinary[j] to big.Int
      let indexInt = new bn(indexIsZeroBinary[j]);

      // Calculate cl, ca, cb, cd
      // cl = Com(l, r)
      cl[j] = pc.PedCom.commitAtIndex(indexInt, r[j], constants.SK);

      // ca = Com(a, s)
      ca[j] = pc.PedCom.commitAtIndex(a[j], s[j], constants.SK);

      // cb = Com(la, t)
      let la = indexInt.mul(a[j]);
      cb[j] = pc.PedCom.commitAtIndex(la, t[j], constants.SK);

      // console.log('a: ', j, ': ', a[j].toArray().join(', '));
      // console.log('u: ', j, ': ', u[j].toArray().join(', '));
    }

    // console.log();

    // Calculate: cd_k = ci^pi,k
    for (let k = 0; k < n; k++) {
      // Calculate pi,k which is coefficient of x^k in polynomial pi(x)
      cd[k] = pc.PedCom.commitAtIndex(new bn(0), u[k], constants.SK);

      for (let i = 0; i < N; i++) {
        // console.log('k: ', k);
        // console.log('i: ', i);
        let iBinary = utils.convertIntToBinary(i, n);
        // console.log('iBinary: ', iBinary);
        // console.log('n: ', n);
        // console.log('a0: ', a[0].toArray().join(', '));
        // console.log('a1: ', a[1].toArray().join(', '));
        // console.log('a2: ', a[2].toArray().join(', '));
        // console.log('indexIsZeroBinary: ', indexIsZeroBinary);

        let pik = GetCoefficient(iBinary, k, n, a, indexIsZeroBinary);

        // console.log('pik: ', pik.toArray().join(', '));
        cd[k] = cd[k].add(this.stmt.commitments[i].mul(pik));
        // console.log('cd ', i, ': ', cd[k].compress().join(', '));
        // console.log();
      }

      // console.log('cd ', k, ': ', cd[k].compress().join(', '));
    }

    // Calculate challenge x
    let x = new bn(0);

    for (let j = 0; j < n; j++) {
      x = zkpUtils.generateChallenge([x.toArray('be', constants.BIG_INT_SIZE), cl[j].compress(), ca[j].compress(), cb[j].compress(), cd[j].compress()]);
    }

    // console.log("Challenge x: ", x.toArray('be', constants.BIG_INT_SIZE));

    // Calculate za, zb zd
    let za = new Array(n); // big int array
    let zb = new Array(n); // big int array
    let f = new Array(n); // big int array

    for (let j = 0; j < n; j++) {
      // f = lx + a
      let indexInt = new bn(indexIsZeroBinary[j]);
      f[j] = indexInt.mul(x);
      f[j] = f[j].add(a[j]);
      f[j] = f[j].umod(P256.n);

      // za = s + rx
      za[j] = r[j].mul(x);
      za[j] = za[j].add(s[j]);
      za[j] = za[j].umod(P256.n);

      // zb = r(x - f) + t
      zb[j] = x.sub(f[j]);
      zb[j] = zb[j].mul(r[j]);
      zb[j] = zb[j].add(t[j]);
      zb[j] = zb[j].umod(P256.n);
    }

    // zd = rand * x^n - sum_{k=0}^{n-1} u[k] * x^k
    let zd = x.pow(new bn(n));
    zd = zd.mul(this.rand);

    // let uxInt = new BigInt(0);
    let sumInt = new bn(0);
    for (let k = 0; k < n; k++) {
      let uxInt = x.pow(new bn(k));
      uxInt = uxInt.mul(u[k]);
      sumInt = sumInt.add(uxInt);
      // sumInt = sumInt.umod(P256.n);
    }

    zd = zd.sub(sumInt);
    zd = zd.umod(P256.n);

    let proof = new OneOutOfManyProof();
    proof.set(this.stmt.commitments, cl, ca, cb, cd, f, za, zb, zd);


    //Test

    // let left1 = pc.PedCom.G[constants.RAND].mul(r[0].mul(x).add(s[0]));
    // let right1 = pc.PedCom.CommitAtIndex(new BigInt(0), za[0], constants.SK);
    //
    // if (left1.eq(right1)){
    //     console.log("Rightttttttttttttttt");
    // } else{
    //     console.log("Wrongggggggggggggggg");
    // }

    // for (let i=0; i<n; i++){
    //     let left1 = cl[i].mul(x).add(ca[i]);
    //     let right1 = pc.PedCom.CommitAtIndex(f[i], za[i], constants.SK);
    //
    //     if (left1.eq(right1)){
    //         console.log("Rightttttttttttttttt");
    //     } else{
    //         console.log("Wrongggggggggggggggg");
    //     }
    // }

    // let leftPoint = P256.curve.point();
    //
    // for (let i = 0; i < N; i++) {
    //     let iBinary = utils.convertIntToBinary(i, n);
    //     let exp = new BigInt(1);
    //     let fji = new BigInt(1);
    //
    //     for (let j = 0; j < n; j++) {
    //         if (iBinary[j] === 1) {
    //             fji = f[j]
    //         } else {
    //             fji = x.sub(f[j]);
    //             fji = fji.umod(P256.n);
    //         }
    //
    //         exp = exp.mul(fji);
    //         exp = exp.umod(P256.n);
    //     }
    //
    //     if (i === 0) {
    //         leftPoint = this.stmt.commitments[i].mul(exp)
    //     } else {
    //         leftPoint = leftPoint.add(this.stmt.commitments[i].mul(exp))
    //     }
    // }
    //
    // let leftPoint2 = P256.curve.point();
    //
    // for (let k = 0; k < n; k++) {
    //     let xk = x.pow(new BigInt(k));
    //     xk = xk.umod(P256.n);
    //
    //     if (k === 0) {
    //         leftPoint2 = cd[k].mul(xk);
    //     } else {
    //         leftPoint2 = leftPoint2.add(cd[k].mul(xk));
    //     }
    // }
    //
    // leftPoint = leftPoint.add(leftPoint2);
    //
    // let rightPoint = pc.PedCom.commitAtIndex(new BigInt(0), zd, constants.SK);
    //
    // if (leftPoint.eq(rightPoint)) {
    //     console.log('Righttttttttt')
    // } else {
    //     console.log('Wrongggggggggg');
    //
    //     let tmpPoint = leftPoint.add(rightPoint.inverse());
    //     console.log('tmpPoint: ', tmpPoint);
    // }

    return {
      proof: proof,
      err: null
    }
  }
}

class OneOutOfManyProof {
  constructor() {
    this.cl = []; // []EllipticPoint
    this.ca = []; // []EllipticPoint
    this.cb = []; // []EllipticPoint
    this.cd = []; // []EllipticPoint
    this.f = []; // [] BigInt
    this.za = []; // [] BigInt
    this.zb = []; // [] BigInt
    this.zd = new bn(0);
    this.stmt = new OneOutOfManyStatement();
  }

  isNull() {
    if (this.cl.length === 0) {
      return true;
    }
    if (this.ca.length === 0) {
      return true;
    }
    if (this.cb.length === 0) {
      return true;
    }
    if (this.cd.length === 0) {
      return true;
    }
    if (this.f.length === 0) {
      return true;
    }
    if (this.za.length === 0) {
      return true;
    }
    if (this.zb.length === 0) {
      return true;
    }
    if (this.zd.eq(0)) {
      return true;
    }
    return this.stmt.commitments.length === 0
  }

  set(commitments, cl, ca, cb, cd, f, za, zb, zd) {
    this.stmt.commitments = commitments;
    this.cl = cl;
    this.ca = ca;
    this.cb = cb;
    this.cd = cd;
    this.f = f;
    this.za = za;
    this.zb = zb;
    this.zd = zd;
    return this;
  }

  toBytes() {
    // if proof is null, return an empty array
    if (this.isNull()) {
      return [];
    }

    let bytes = new Uint8Array(constants.ONE_OF_MANY_PROOF_SIZE);
    let offset = 0;

    // N = 2^n
    let N = constants.CM_RING_SIZE;
    let n = constants.CM_RING_SIZE_EXP;

    // convert array cl to bytes array
    for (let i = 0; i < n; i++) {
      bytes.set(this.cl[i].compress(), offset);
      offset += constants.COMPRESS_POINT_SIZE;
    }
    // convert array ca to bytes array
    for (let i = 0; i < n; i++) {
      bytes.set(this.ca[i].compress(), offset);
      offset += constants.COMPRESS_POINT_SIZE;
    }

    // convert array cb to bytes array
    for (let i = 0; i < n; i++) {
      bytes.set(this.cb[i].compress(), offset);
      offset += constants.COMPRESS_POINT_SIZE;
    }

    // convert array cd to bytes array
    for (let i = 0; i < n; i++) {
      bytes.set(this.cd[i].compress(), offset);
      offset += constants.COMPRESS_POINT_SIZE;
    }

    // convert array f to bytes array
    for (let i = 0; i < n; i++) {
      bytes.set(this.f[i].toArray('be', constants.BIG_INT_SIZE), offset);
      offset += constants.BIG_INT_SIZE;
    }

    // convert array za to bytes array
    for (let i = 0; i < n; i++) {
      bytes.set(this.za[i].toArray('be', constants.BIG_INT_SIZE), offset);
      offset += constants.BIG_INT_SIZE;
    }

    // convert array zb to bytes array
    for (let i = 0; i < n; i++) {
      bytes.set(this.zb[i].toArray('be', constants.BIG_INT_SIZE), offset);
      offset += constants.BIG_INT_SIZE;
    }

    bytes.set(this.zd.toArray('be', constants.BIG_INT_SIZE), offset);
    return bytes;
  }

  verify() {
    let N = this.stmt.commitments.length;

    // the number of Commitment list's elements must be equal to CMRingSize
    if (N !== constants.CM_RING_SIZE) {
      return false
    }
    let n = constants.CM_RING_SIZE_EXP;

    //Calculate challenge x
    let x = new bn(0);
    for (let j = 0; j < n; j++) {
      x = zkpUtils.generateChallenge([utils.addPaddingBigInt(x, constants.BIG_INT_SIZE), this.cl[j].compress(), this.ca[j].compress(), this.cb[j].compress(), this.cd[j].compress()])
    }

    for (let i = 0; i < n; i++) {
      //Check cl^x * ca = Com(f, za)
      let leftPoint1 = this.cl[i].mul(x).add(this.ca[i]);
      let rightPoint1 = pc.PedCom.commitAtIndex(this.f[i], this.za[i], constants.SK);

      if (!leftPoint1.eq(rightPoint1)) {
        return false;
      }

      //Check cl^(x-f) * cb = Com(0, zb)s
      let xSubF = x.sub(this.f[i]);
      xSubF = xSubF.umod(P256.n);

      let leftPoint2 = this.cl[i].mul(xSubF).add(this.cb[i]);
      let rightPoint2 = pc.PedCom.commitAtIndex(new bn(0), this.zb[i], constants.SK);

      if (!leftPoint2.eq(rightPoint2)) {
        return false
      }
    }

    let leftPoint3 = P256.curve.point(0, 0);
    let leftPoint32 = P256.curve.point(0, 0);

    for (let i = 0; i < N; i++) {
      let iBinary = utils.convertIntToBinary(i, n);

      let exp = new bn(1);
      let fji = new bn(0);

      for (let j = 0; j < n; j++) {
        if (iBinary[j] === 1) {
          fji = this.f[j];
        } else {
          fji = x.sub(this.f[j]);
          fji = fji.umod(P256.n);
        }

        exp = exp.mul(fji);
        exp = exp.umod(P256.n);
      }
      if (i === 0) {
        leftPoint3 = this.stmt.commitments[i].mul(exp);
      } else {
        leftPoint3 = leftPoint3.add(this.stmt.commitments[i].mul(exp))
      }
    }

    for (let k = 0; k < n; k++) {
      let xk = x.pow(new bn(k));
      xk = P256.n.sub(xk);
      xk = xk.umod(P256.n);

      if (k === 0) {
        leftPoint32 = this.cd[k].mul(xk);
      } else {
        leftPoint32 = leftPoint32.add(this.cd[k].mul(xk));
      }
    }

    leftPoint3 = leftPoint3.add(leftPoint32);

    let rightPoint3 = pc.PedCom.commitAtIndex(new bn(0), this.zd, constants.SK);

    return leftPoint3.eq(rightPoint3);
  }
}

// Get coefficient of x^k in the polynomial p_i(x)
function GetCoefficient(iBinary, k, n, a, l) {
  let res = new poly.Poly([new bn(1)]);
  let fji = new poly.Poly([]);

  for (let j = n - 1; j >= 0; j--) {
    let fj = new poly.Poly([a[j], new bn(l[j])]);
    if (iBinary[j] === 0) {
      fji = new poly.Poly([new bn(0), new bn(1)]).sub(fj, P256.n);
    } else {
      fji = fj;
    }
    res = res.mul(fji, P256.n);
  }

  if (res.getDegree() < k) {
    return new bn(0);
  }

  return res.Coeffs[k];
}

