import bn from 'bn.js';
import { N } from 'privacy-js-lib/lib/ec'
import { randScalar, hashSha3BytesToBytes } from 'privacy-js-lib/lib/privacy_utils'
import { PedCom } from 'privacy-js-lib/lib/pedersen'
import { SK, RAND, BIG_INT_SIZE} from 'privacy-js-lib/lib/constants'
import { HASH_SIZE } from './constants';

// PK = G^SK + H^randomness
class SchnPrivKey {
  constructor(sk, r) {
    this.SK = sk;
    this.R = r;
    this.PK = new SchnPubKey();
    this.PK.G = PedCom.G[SK];
    this.PK.H = PedCom.G[RAND];
    this.PK.PK = this.PK.G.mul(this.SK).add(this.PK.H.mul(this.R));
  }

  sign(data) {
    console.log("SIGNING TX.....");
    if (data.length != HASH_SIZE) {
      throw new Error("Hash length must be 32");
    }
    let s1 = randScalar();
    let s2 = randScalar();
    let t = (this.R == 0) ? (this.PK.G.mul(s1.toRed(N).fromRed())) : (this.PK.G.mul(s1.toRed(N).fromRed())).add(this.PK.H.mul(s2.toRed(N).fromRed()));
    let E = hash(t, data);

    let Z1 = s1.toRed(N).redSub(this.SK.toRed(N).redMul(E.toRed(N))).fromRed();

    if (this.R != 0) {
      let Z2 = s2.toRed(N).redSub(this.R.toRed(N).redMul(E.toRed(N))).fromRed();
      let res = new Uint8Array(BIG_INT_SIZE * 3);
      res.set(E.toArray('be', BIG_INT_SIZE), 0);
      res.set(Z1.toArray('be', BIG_INT_SIZE), BIG_INT_SIZE);
      res.set(Z2.toArray('be', BIG_INT_SIZE), BIG_INT_SIZE * 2);
      return res;
    } else {
      let res = new Uint8Array(BIG_INT_SIZE * 2);
      res.set(E.toArray('be', BIG_INT_SIZE), 0);
      res.set(Z1.toArray('be', BIG_INT_SIZE), BIG_INT_SIZE);
      return res;
    }
  }
}

class SchnPubKey {
  constructor(PK, G, H) {
    this.PK = PK;
    this.G = G;
    this.H = H;
  }

  verify(signaturesBytesArrays, data) {
    let E = new bn(signaturesBytesArrays.slice(0, BIG_INT_SIZE), 10, 'be');
    let S1 = new bn(signaturesBytesArrays.slice(BIG_INT_SIZE, 2 * BIG_INT_SIZE), 10, 'be');

    if (signaturesBytesArrays.length == BIG_INT_SIZE * 2) {
      let rv = (this.PK.mul(E)).add(this.G.mul(S1));
      let ev = hash(rv, data);
      return E.eq(ev);
    }

    let S2 = new bn(signaturesBytesArrays.slice(BIG_INT_SIZE * 2, 3 * BIG_INT_SIZE), 10, 'be');
    let rv = (this.PK.mul(E)).add(this.H.mul(S2)).add(this.G.mul(S1));
    let ev = hash(rv, data);
    return E.eq(ev);
  }
}

// hash hashes point (X, Y) and bytes array
function hash(point, bytes) {
  let b = new Uint8Array(BIG_INT_SIZE * 2 + bytes.length);
  b.set(point.getX().toArray('be', BIG_INT_SIZE), 0);
  b.set(point.getY().toArray('be', BIG_INT_SIZE), BIG_INT_SIZE);
  b.set(bytes, BIG_INT_SIZE * 2);

  return new bn(hashSha3BytesToBytes(b));
}

export  {
  SchnPrivKey,
  SchnPubKey
}
